    padding: 20px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    margin: 16px;
    border: 1px solid rgba(255, 253, 253, 0.973);
    border-radius: 10px;
    -webkit-box-shadow: 0 13px 50px rgba(204, 204, 204, 0.301);
            box-shadow: 0 13px 50px rgba(204, 204, 204, 0.301);
    font-weight: bold;
    text-align: center;
    cursor: pointer;
